.creation-form {
    margin: 30px 0 100px 0;

    .container {
        display: flex;
        justify-content: space-between;
    }

    .custom-select .custom-select-item {
        font-size: 20px;
        line-height: 30px;
    }
}

.creation-form-left {
    width: calc(100% - 450px);
}

.select-block {
    max-width: 383px;
    margin-bottom: 50px;

    &:last-child {
        margin-bottom: 0;
    }
}

.form-block-title {
    font-weight: 700;
    font-size: 18px;
    line-height: 28px;
    color: #606060;
    margin-bottom: 5px;
}

.creation-form-detail {
    margin-top: 100px;
}

.creation-form-detail__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 30px;
    color: #761CBD;
    margin-bottom: 30px;
}

.input-block {
    max-width: 520px;

    input {
        border: 1px solid #BCBCBC;
        border-radius: 20px;
        height: 40px;
        width: 100%;
        padding: 0 15px;
        font-weight: 400;
        font-size: 18px;
        line-height: 30px;
        color: #000000;
    }
}

.textarea-block {
    max-width: 520px;
    margin-top: 30px;

    textarea {
        border: 1px solid #BCBCBC;
        border-radius: 20px;
        height: 120px;
        min-height: 120px;
        width: 100%;
        padding: 15px 30px;
        font-weight: 400;
        font-size: 20px;
        line-height: 30px;
        color: #000000;
        max-width: 100%;
        min-width: 100%;
        -webkit-appearance: none;
        background-image: url('../img/textarea.svg');
        background-repeat: no-repeat;
        background-position: calc(100% - 10px) calc(100% - 10px);

        &::-webkit-resizer {
            display: none;
        }
    }
}

.textarea-block__bottom {
    margin-top: 6px;
    display: flex;
    justify-content: space-between;
}

.textarea__btn {
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-decoration-line: underline;
    color: #FF8A00;
    display: flex;

    span {
        font-weight: 400;
    }
}

.input-file {
    position: relative;
    font-weight: 700;
    font-size: 16px;
    line-height: 19px;
    text-decoration-line: underline;
    color: #FF8A00;
    display: flex;
    cursor: pointer;

    &.btn--purple {
        font-size: 16px;
        line-height: 18px;
        text-decoration: none;
    }

    input {
        position: absolute;
        width: 100% !important;
        height: 100%;
        left: 0;
        top: 0;
        opacity: 0;
        margin: 0 !important;
    }
}

.creation-form-date {
    margin-top: 100px;

    .creation-form-title {
        margin-bottom: 30px;
    }
}

.creation-form-date__wrapper {
    position: relative;
    padding: 0 60px;
    user-select: none;

    .swiper-button-next,
    .swiper-button-prev {
        &::after {
            font-size: 24px;
            color: #FF8A00;
            font-weight: 900;
        }
    }
}

.creation-form-title {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    color: #606060;
}

.creation-form-swiper {
    .swiper-slide {
        width: auto;
    }
}

.creation-form-date-item {
    background: #EEEEEE;
    width: 96px;
    height: 96px;
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease-in-out;

    &:hover {
        background: #d9d9d9;
    }

    &.active {
        background: #FF8A00;

        .creation-form-date-item__num,
        .creation-form-date-item__top,
        .creation-form-date-item__today {
            color: #fff;
        }
    }

    input {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        opacity: 0;
        cursor: pointer;
    }
}

.creation-form-date-item__top {
    font-weight: 300;
    font-size: 10px;
    line-height: 11px;
    color: #000000;
}

.creation-form-date-item__num {
    font-weight: 700;
    font-size: 44px;
    line-height: 44px;
    color: #FF8A00;
}

.creation-form-date-item__today {
    font-weight: 700;
    font-size: 7px;
    line-height: 11px;
    color: #000000;
    position: absolute;
    bottom: 10px;
}

.creation-form-time {
    margin-top: 50px;
}

.creation-form-time__wrapper {
    margin-top: 14px;
    display: flex;
    align-items: center;
}

.radio-block {
    align-items: center;
    display: flex;

    input {
        appearance: none;
        width: 22px;
        height: 22px;
        border: 1px solid #BCBCBC;
        border-radius: 50%;
        position: relative;
        margin-right: 10px;
        cursor: pointer;

        &::before {
            position: absolute;
            content: '';
            background: #FF8A00;
            width: 14px;
            height: 14px;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            border-radius: 50%;
            opacity: 0;
            transition: all 0.3s ease-in-out;
        }

        &:checked::before {
            opacity: 1;
        }
    }
}

.radio-block-selects {
    display: flex;
    align-items: center;
    margin-left: 100px;

    p {
        font-weight: 300;
        font-size: 20px;
        line-height: 30px;
        color: #BCBCBC;
        margin: 0 10px;
    }

    .custom-select {
        width: 127px;
    }

    .custom-select .custom-select__top {
        padding: 9px 20px;
    }
}

.creation-form-suma {
    margin-top: 100px;
}

.creation-form-suma__top {
    background: #F8F3FB;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px 50px;
    margin-top: 22px;

    p {
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #606060;

        span {
            font-weight: 700;
        }
    }

    .checkbox-block {
        font-weight: 700;
    }
}

.checkbox-block {
    display: flex;
    align-items: center;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #606060;

    input {
        appearance: none;
        border: 1px solid #761CBD;
        border-radius: 7px;
        width: 29px;
        min-width: 29px;
        height: 29px;
        cursor: pointer;
        margin-right: 11px;
        position: relative;
        overflow: hidden;
        transition: all 0.3s ease-in-out;

        &::before {
            content: '';
            position: absolute;
            background-image: url('../../img/check.svg');
            background-repeat: no-repeat;
            background-size: cover;
            width: 28px;
            height: 28px;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            opacity: 0;
            transition: all 0.3s ease-in-out;
        }

        &:checked {
            background: #761CBD;
        }

        &:checked::before {
            opacity: 1;
        }
    }
}

.creation-form-suma__bottom {
    background: #761CBD;
    padding: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .input-block {
        display: flex;
        align-items: center;
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        color: #FFFFFF;
        width: 350px;

        input {
            margin-right: 10px;
        }
    }

    .checkbox-block {
        color: #FFFFFF;
    }

    .checkbox-block input {
        border: 1px solid #FFFFFF;
    }
}

.creation-form__btn {
    &:hover {
        color: #FFFFFF !important;
    }
}

.creation-form-right {
    width: 420px;
}

.creation-form-right__title {
    font-weight: 700;
    font-size: 28px;
    line-height: 48px;
    color: #606060;
    text-align: center;
}

.creation-form-right__subtitle {
    font-weight: 400;
    font-size: 18px;
    line-height: 30px;
    text-transform: uppercase;
    color: #606060;
    text-align: center;
}

.creation-form-right__wrapper {
    margin-top: 20px;

    .person-row {
        flex-direction: column;
        align-items: flex-start;
        padding: 20px;
        padding-left: 125px;
    }

    .person-row .person-block {
        transform: translateX(-100px);

    }

    .star {
        margin-bottom: 30px;
    }

    .person-info {
        margin-bottom: 30px;
    }
}



@media (max-width: 1550px) {
    .creation-form-suma__bottom {
        padding: 30px;
    }

    .creation-form-suma__top {
        padding: 20px 30px;
    }

    .creation-form-suma__bottom .input-block {
        width: 250px;
    }
}

@media (max-width: 1400px) {
    .creation-form-right__wrapper .person-row .person-block {
        transform: translateX(-90px);
    }

    .creation-form-right__wrapper .person-row {
        padding-left: 110px;
    }

    .creation-form-right {
        width: 365px;
    }

    .creation-form-left {
        width: calc(100% - 400px);
    }

    .creation-form-right__wrapper .star {
        margin-top: 15px;
    }

    .creation-form-detail {
        margin-top: 50px;
    }

    .creation-form-detail__title {
        font-size: 25px;
        line-height: 27px;
    }

    .select-block {
        margin-bottom: 20px;
        max-width: 100%;
    }

    .input-block {
        max-width: 100%;
    }

    .textarea-block {
        max-width: 100%;
    }

    .form-block-title {
        font-size: 16px;
        line-height: 27px;
    }

    .creation-form-date {
        margin-top: 50px;
    }

    .creation-form-title {
        font-size: 17px;
        line-height: 27px;
    }

    .creation-form-suma {
        margin-top: 50px;
    }


    .creation-form-right__title {
        font-size: 32px;
        line-height: 48px;
    }

    .creation-form-right__subtitle {
        font-size: 18px;
        line-height: 27px;
    }
}

@media (max-width: 1220px) {
    .creation-form .container {
        flex-direction: column;
    }

    .creation-form-left {
        width: 100%;
    }

    .creation-form-right {
        width: 100%;
        margin-top: 30px;
    }

    .creation-form-right__wrapper {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    .creation-form-right__wrapper .person-row {
        padding: 20px;
        align-items: center;
        margin-bottom: 0;
    }

    .creation-form-right__wrapper .person-row .person-block {
        transform: translateX(0);
    }

    .creation-form-right__wrapper .person-row {
        display: flex !important;
        align-items: center !important;

        .person-row__btn {
            margin-left: 0;
        }
    }
}


@media (max-width: 780px) {
    .creation-form-suma__bottom {
        flex-direction: column;
    }

    .creation-form-suma__bottom .checkbox-block {
        margin: 20px 0;
    }

    .creation-form-suma__top {
        flex-direction: column;
    }

    .creation-form-suma__top p {
        margin-bottom: 20px;
    }

    .creation-form-time__wrapper {
        flex-direction: column;
        align-items: flex-start;
    }

    .radio-block-selects {
        margin-left: 0;
        margin-top: 20px;
    }

    .creation-form-detail__title {
        font-size: 22px;
        line-height: 25px;
        margin-bottom: 20px;
    }

    .textarea-block {
        margin-top: 20px;
    }

    .custom-select .custom-select-value {
        font-size: 18px;
        line-height: 28px;
    }

    .custom-select .custom-select__top::after {
        top: 6px;
    }

    .creation-form-date-item {
        width: 70px;
        height: 70px;
    }

    .creation-form-date-item__num {
        font-size: 30px;
        line-height: 30px;
    }

    .creation-form-date-item__top {
        font-size: 8px;
        line-height: 9px;
    }

    .creation-form-date-item__today {
        bottom: 6px;
        font-size: 5px;
    }

    .creation-form-date__wrapper {
        padding: 0 30px;

        .swiper-button-next {
            right: 0;
        }

        .swiper-button-prev {
            left: 0;
        }
    }

    .textarea-block__bottom {
        flex-direction: column;
    }

    .textarea__btn {
        margin-bottom: 10px;
    }

    .creation-form-right__wrapper {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 580px) {
    .creation-form-right__wrapper {
        grid-template-columns: repeat(1, 1fr);
    }

    .creation-form-right__wrapper .person-info {
        margin-bottom: 0;
    }

    .creation-form-right__wrapper .star {
        margin-bottom: 0;
    }

    .creation-form-suma__top,
    .creation-form-suma__bottom {
        width: calc(100% + 30px);
        transform: translateX(-15px);
    }
}