.footer {
    background: #393939;
    margin-top: auto;
}

.container {}

.footer__wrapper {
    padding: 50px 0;
    display: flex;
    justify-content: center;
    position: relative;
    padding-left: 140px;
}

.footer__left {
    position: absolute;
    left: 0;

}

.footer__logo {}

.soc-block {
    margin-top: 10px;
    display: flex;

    a {
        display: block;
        margin-right: 20px;
        transition: all 0.3s ease-in-out;

        @media(any-hover: hover) {
            &:hover {
                opacity: 0.6;
            }
        }
    }
}

.footer__right {
    display: flex;
    justify-content: space-between;
    max-width: 650px;
    width: 100%;
}

.footer-list {
    width: fit-content;

    a {
        font-weight: 400;
        font-size: 18px;
        line-height: 22px;
        text-decoration-line: underline;
        color: #FFFFFF;
        display: block;
        margin-bottom: 27px;
        transition: all 0.3s ease-in-out;

        @media(any-hover: hover) {
            &:hover {
                text-shadow: .5px 0 0 currentColor;
            }
        }
    }
}

.footer-list__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 29px;
    color: #FFFFFF;
    margin-bottom: 12px;
}

.footer__bottom {
    background: #606060;
    padding: 18px 0;

    .container {
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        color: #BCBCBC;
        text-align: center;
    }
}


@media (max-width: 1200px) {
    .footer__left {
        position: static;
    }

    .footer__wrapper {
        padding: 40px 0;
        justify-content: space-between;
    }

    .footer__right {
        max-width: 540px;
    }
}

@media (max-width: 840px) {
    .footer__wrapper {
        flex-direction: column;
    }

    .footer__right {
        max-width: 100%;
        margin-top: 30px;
    }
}

@media (max-width: 580px) {
    .footer__right {
        flex-direction: column;
    }
}