.active-orders {
    margin: 100px 0 50px 0;
}

.container {}

.active-orders__title {}

.title {}

.active-orders__wrapper {
    margin-top: 50px;

    .offers-row {}

    .row-item {
        align-items: center;
    }

    .offers-row-left {
        p {
            font-weight: 400;
            font-size: 13px;
            line-height: 17px;
            color: #606060;
            margin-top: 10px;
            margin-left: 38px;

            span {
                font-weight: 700;
            }
        }
    }

    .offers-row-left__top {
        align-items: flex-start;

        &::before {
            transform: translateY(10px);
        }
    }

    .offers-row-center {
        max-width: 570px;
    }

    .offers-row-right {
        flex-direction: row;
        justify-content: space-between;
        min-width: auto !important;
        max-width: 440px;
        width: 100%;
    }

    .offers-row__price {
        margin-right: 20px;
    }

    .offers-row__btn {
        margin-top: 0;
    }
}

.active-orders__btn {
    margin: 50px auto 0 auto;
}


@media (max-width: 860px) {
    .active-orders__wrapper .offers-row-right {
        flex-direction: column;
    }

    .active-orders__wrapper .offers-row-left p {
        margin-left: 0;
        text-align: center;
    }

    .active-orders__wrapper .offers-row__btn {
        margin-top: 10px;
    }

    .active-orders {
        margin: 40px 0;
    }

    .active-orders__wrapper {
        margin-top: 30px;
    }
}


