.profile {
    margin: 50px 0 100px 0;

    .tab-link-wrapper {
        margin-top: 50px;
        display: flex;
        justify-content: space-around;
        border-bottom: 1px solid #761CBD;
    }

    .tab {
        font-weight: 400;
        font-size: 24px;
        line-height: 30px;
        color: #606060;
        padding-bottom: 12px;

    }

    .tab-active {
        text-shadow: 1px 0 0 currentColor;
        color: #761CBD;
        border-bottom: 5px solid #761CBD;
    }

    .tab-content-wrapper {
        margin-top: 30px;
    }

    .tabs-content {
        display: none;
    }

    .tabs-content-active {
        display: block;
    }

}

.profile-top {
    display: flex;
    align-items: center;

    .person-block {
        align-items: center;
    }

    .person-block__img {
        width: 80px;
        height: 80px;
    }

    .person-block__name {
        margin-top: 0;
    }
}

.name-edit {
    background-image: url('../../img/edit.svg');
    background-size: cover;

    width: 26px;
    height: 26px;
    margin-left: 50px;
}

.general-information {
    .order-box {
        grid-template-columns: repeat(3, 1fr);
        max-width: 840px;
    }

    h4 {
        font-weight: 700;
        font-size: 20px;
        line-height: 30px;
        color: #606060;
        margin-bottom: 10px;
        margin-top: 30px;
    }

    p {
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        color: #000000;
    }

    a {
        font-weight: 700;
        font-size: 20px;
        line-height: 30px;
        color: #606060;
        display: block;
        margin-bottom: 20px;
        width: fit-content;

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.bottom-link {
    margin-top: 30px;
}

.purple-block-wrap {
    display: flex;
    flex-wrap: wrap;
}

.purple-block {
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    color: #FFFFFF;
    padding: 5px 15px;
    background: linear-gradient(145.04deg, #761CBD 12.27%, #CD8EFF 87.55%);
    border-radius: 22px;
    margin-right: 20px;
    margin-bottom: 20px;
}

.remove {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    text-decoration-line: underline;
    color: #606060;
    margin-top: 130px;
    margin-left: auto;
    display: block;
}

.portfolio {
    padding-top: 20px;
}

.portfolio-block {
    background: #F8F3FB;
    border: 1px dashed #761CBD;
    border-radius: 33px;
    min-height: 250px;
    width: 100%;
    max-width: 1070px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    padding: 30px;

    &:first-child {
        margin-bottom: 20px;
    }

    input {
        width: 400px;
        margin: 0 20px;
    }
}

.portfolio-block__icon {
    width: 26px;
    height: 26px;
}

.portfolio-block__text {
    font-weight: 400;
    font-size: 23.2px;
    line-height: 27px;
    color: #000000;
    margin: 0 20px;
}

.portfolio-catalog {
    margin-top: 50px;
    padding-top: 50px;
    position: relative;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;

    &::before {
        position: absolute;
        content: '';
        width: 200vw;
        background: #761CBD;
        height: 1px;
        left: -50%;
        top: 0;
    }
}

.portfolio-catalog-item__top {
    display: block;

    img {
        width: 100%;
        object-fit: cover;
        height: 325px;
    }
}


.portfolio-catalog-item__title {
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    color: #000000;
    margin-top: 10px;
    height: 43px;
    @include text-hide(2);
}

.portfolio-catalog-item__bottom {
    margin-top: 15px;
    padding-top: 10px;
    border-top: 1px solid #E0E0E0;
    display: flex;
    justify-content: space-between;
}

.portfolio-catalog-item__remove {
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    text-decoration-line: underline;
    color: #606060;

    &:hover {
        text-shadow: .5px 0 0 currentColor;
    }
}

.portfolio-catalog-item__edit {
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    text-decoration-line: underline;
    color: #761CBD;

    &:hover {
        text-shadow: .5px 0 0 currentColor;
    }
}

.portfolio-catalog-item--video {
    position: relative;

    &::before {
        content: '';
        position: absolute;
        background-image: url('../../img/youtube.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: 70px;
        height: 70px;
        top: 135px;
        left: 50%;
        transform: translateX(-50%);
        pointer-events: none;
    }
}


.change-password {
    max-width: 383px;
    width: 100%;

    .input-block {
        max-width: 100%;
        margin-bottom: 30px;
    }
}


@media (max-width: 1400px) {
    .profile .tab {
        font-size: 24px;
        line-height: 30px;
    }

    .portfolio-catalog {
        grid-template-columns: repeat(3, 1fr);
    }

    .input-file.btn--purple {
        padding: 8px 30px;
        font-size: 16px;
    }

    .portfolio-block__text {
        font-size: 20.2px;
    }

    .portfolio-block input {
        width: 320px;
    }

    .portfolio-catalog-item__title {
        font-size: 18px;
        line-height: 23px;
        height: 46px;
    }

    .portfolio-catalog-item__edit {
        font-size: 18.1438px;
    }

    .portfolio-catalog-item__remove {
        font-size: 18.1438px;
    }

    .portfolio-catalog-item__top img {
        height: 330px;
    }
}

@media (max-width: 1023px) {
    .portfolio-catalog {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 780px) {
    .profile .tab-link-wrapper {
        justify-content: flex-start;
        overflow-x: auto;
    }

    .profile .tab {
        display: block;
        white-space: nowrap;
        margin-right: 40px;
        font-size: 22px;
        line-height: 27px;
        padding-bottom: 10px;

        &:last-child {
            margin-right: 0;
        }
    }

    .general-information .order-box {
        grid-template-columns: 1fr;
        gap: 20px;
    }

    .name-edit {
        margin-left: 30px;
        width: 30px;
        height: 30px;
    }

    .profile-top .person-block__img {
        width: 70px;
        height: 70px;
    }

    .profile-top {
        .person-block .person-block__name {
            font-size: 17px;
            line-height: 22px;
        }
    }

    .purple-block {
        font-size: 14px;
        line-height: 25px;
        margin-right: 10px;
        margin-bottom: 10px;
    }

    .general-information h4 {
        font-size: 18px;
        line-height: 28px;
    }

    .general-information p {
        font-size: 14px;
    }

    .portfolio-block {
        flex-direction: column;
        min-height: 250px;
    }

    .portfolio-block__text {
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .portfolio-block input {
        margin-top: 20px;
        margin-bottom: 20px;
    }

    .portfolio-catalog {
        grid-template-columns: 1fr;
    }

    .profile .tab-link-wrapper {
        margin-top: 30px;
    }

    .profile .tab-content-wrapper {
        margin-top: 20px;
    }

    .general-information a {
        font-size: 18px;
    }

    .remove {
        font-size: 18px;
    }
}