.top-ban {
    position: relative;
    z-index: 1;
    padding: 60px 0;

    .container {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }

    &.active-orders-ban {
        background: #2F0B4C;
    }
}

.active-orders-top {
    display: flex;
    align-items: center;

    .top-ban__btn {
        margin-top: 0;
        margin-left: 30px;
    }

    .top-ban__title {
        margin-top: 0;
        font-size: 32px;
        line-height: 30px;
        text-transform: none;
    }
}

.top-ban__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    object-fit: cover;
    z-index: -1;
}



.top-ban__category {
    font-weight: 300;
    font-size: 20px;
    line-height: 30px;
    text-transform: uppercase;
    color: #FFFFFF;
}

.top-ban__title {
    font-weight: 900;
    font-size: 40px;
    line-height: 40px;
    text-transform: uppercase;
    color: #FFFFFF;
    margin-top: 30px;
}

.top-ban__btn {
    margin-top: 30px;

    &:hover {
        color: #FFFFFF !important;
    }
}

.top-ban__btn-two {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    text-decoration-line: underline;
    color: #BCBCBC;
    margin-top: 20px;

    &:hover {
        color: #BCBCBC;
        text-shadow: .5px 0 0 currentColor;
        text-decoration-line: underline;
    }
}


@media (max-width: 1400px) {
    .top-ban__category {
        font-size: 16px;
    }

    .top-ban__title {
        font-size: 32px;
        margin-top: 20px;
    }

    .top-ban__btn {
        margin-top: 20px;
    }

    .active-orders-top .top-ban__title {
        font-size: 24px;
        line-height: 23px;
    }
}

@media (max-width: 860px) {
    .active-orders-top {
        flex-direction: column;
    }

    .active-orders-top .top-ban__title {
        margin-bottom: 20px;
    }
}

@media (max-width: 580px) {
    .top-ban__title {
        font-size: 28px;
    }
}