.reviews {
    padding: 100px 0;

    @media (max-width: 1400px) {
        padding: 50px 0;
    }
}

.container {}

.reviews__title {}

.title {}

.reviews__wrapper {
    margin: 30px auto 0 auto;
    position: relative;
    padding: 0 115px;
    max-width: 1300px;

    .swiper {
        padding-bottom: 30px;
        border-radius: 30px;
    }

    .swiper-button-next,
    .swiper-button-prev {
        width: 70px;
        height: 70px;
        background: #F8F3FB;
        border-radius: 50%;
        transition: all 0.3s ease-in-out;
        top: 42%;

        @media(any-hover: hover) {
            &:hover {
                background: #efdcfa;
            }
        }

        &::after {
            color: #FF8A00;
            font-size: 22px;
            font-weight: 900;
        }
    }

    .swiper-button-next {}

    .swiper-button-prev {}

    .swiper-pagination {
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 6px;
    }

    .swiper-pagination-bullet {
        background: #FF8A00;
        border-radius: 10px;
        width: 50px;
        height: 2px;
        opacity: 1;
        transition: all 0.1s ease-in-out;
    }

    .swiper-pagination-bullet-active {
        height: 6px;
    }

    .swiper-slide {
        border-radius: 30px;
    }
}



.-swiper {}

.swiper-wrapper {}

.swiper-slide {}

.reviews-item {
    background: #FFF4E6;
    border-radius: 30px;
    max-width: 1070px;
    margin: 0 auto;
    padding: 50px 70px 50px 140px;
}

.reviews-item__top {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .person-block {
        align-items: center;
    }

    .person-block__img {
        width: 90px;
        height: 90px;
    }

    .person-block__info {
        margin-top: 0;
        margin-left: 30px;
    }

    .person-block__name {
        font-size: 24px;
        line-height: 29px;
        color: #FF8A00;
        margin-top: 0;
    }

    .person-block__online {
        font-size: 16px;
        line-height: 19px;
        color: #606060;
        margin-top: 10px;
    }
}



.reviews-item__top--icon {
    width: 64px;
    height: 64px;
    background: #FFFFFF;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 15px;

    img {
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
    }
}

.reviews-item__bottom {
    display: flex;

}

.reviews-item__icon {
    margin-top: 40px;
    margin-right: 44px;
    margin-left: 16px;
    width: 60px;
    min-width: 60px;

    img {
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
    }
}

.reviews-item__descr {
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    color: #000000;
}


@media (max-width: 1400px) {
    .reviews-item {
        padding: 50px;
    }

    .reviews-item__top .person-block__img {
        width: 90px;
        height: 90px;
    }

    .reviews-item__top .person-block__name {
        margin-top: 0;
        font-size: 18px;
        line-height: 22px;
    }

    .reviews-item__top .person-block__online {
        font-size: 14px;
        line-height: 17px;
    }

    .reviews-item__top .person-block__info {
        margin-left: 20px;
    }

    .reviews-item__descr {
        font-size: 16px;
        line-height: 26px;
    }

    .reviews-item__icon {
        width: 70px;
        min-width: 70px;
        margin-top: 20px;
        margin-right: 30px;

        img {
            max-width: 100%;
            max-height: 100%;
            object-fit: cover;
        }
    }

    .reviews-item__top--icon {
        padding: 15px;

        img {
            max-width: 100%;
            max-height: 100%;
            object-fit: cover;
        }
    }

    .reviews-item__top--icon {
        width: 60px;
        height: 60px;
    }

    .reviews__wrapper .swiper-button-next,
    .reviews__wrapper .swiper-button-prev {
        width: 55px;
        height: 55px;
    }

    .reviews__wrapper {
        padding: 0 90px;
    }
}


@media (max-width: 780px) {

    .reviews__wrapper .swiper-button-next,
    .reviews__wrapper .swiper-button-prev {
        display: none;
    }

    .reviews__wrapper {
        padding: 0;
    }
}

@media (max-width: 580px) {
    .reviews-item {
        padding: 30px 15px;
    }

    .reviews-item__descr {
        font-size: 14px;
        line-height: 24px;
        margin-top: 15px;
    }

    .reviews-item__icon {
        width: 50px;
        min-width: 50px;
        display: none;
    }

    .reviews-item__top .person-block__img {
        width: 70px;
        height: 70px;
    }

    .reviews-item__top .person-block__online {
        margin-top: 5px;
    }

    .reviews-item__top--icon {
        width: 45px;
        height: 45px;
    }
}