.header {
    padding: 15px 0;
    background: #F8F3FB;

    .container {
        display: flex;
        align-items: center;
    }
}



.logo {
    height: fit-content;
    z-index: 222222;

    img {
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
        display: block;
    }
}

.header-btns {
    margin-left: auto;
    display: flex;
    align-items: center;
    margin-right: 95px;
}

.header__btn {
    font-weight: 700;
    font-size: 18px;
    line-height: 20px;
    color: #761CBD;
    margin-right: 50px;

    @media(any-hover: hover) {
        &:hover {
            color: #761CBD;
            opacity: 0.6;
        }
    }

    &:last-child {
        margin-right: 0;
    }
}



.lang {
    position: relative;

    @media(any-hover: hover) {
        &:hover {
            .lang__list {
                opacity: 1;
                pointer-events: all;
            }

            .lang-act {
                &::after {
                    transform: rotateX(180deg);
                }
            }
        }
    }
}

.lang-act {
    font-weight: 700;
    font-size: 22.1438px;
    line-height: 27px;
    color: #761CBD;
    display: flex;
    align-items: center;
    cursor: pointer;

    &::after {
        content: '';
        background-image: url('../img/arrow-orange.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: 28px;
        height: 28px;
        display: block;
        margin-left: 1px;
        transition: all 0.3s ease-in-out;
    }
}

.lang__list {
    position: absolute;
    background: #FFFFFF;
    box-shadow: 4px 4px 15px rgba(67, 0, 119, 0.18);
    border-radius: 10px;
    padding: 15px;
    opacity: 0;
    pointer-events: none;
    transition: all 0.3s ease-in-out;
    z-index: 2;
}

.lang__link {
    font-weight: 600;
    font-size: 20px;
    line-height: 27px;
    color: #761CBD;
    margin-bottom: 10px;
    display: block;
    transition: all 0.3s ease-in-out;

    @media(any-hover: hover) {
        &:hover {
            color: #9b3ee2;
        }
    }

    &:last-child {
        margin-bottom: 0;
    }
}

.header-person-mob {
    display: none !important;
}

.header-person {
    display: none;
    margin-left: auto;
    align-items: center;
}

.basket {
    &::before {
        background-image: url('../img/briefcase.svg');
    }
}

.hed-circle {
    background: #761CBD;
    padding: 12px;
    border-radius: 50px;
    white-space: nowrap;
    display: flex;
    align-items: center;
    margin-right: 30px;
    position: relative;
    transition: all 0.3s ease-in-out;

    &:hover {
        color: #FFFFFF;
        background: #8b27d7;
        text-decoration-line: underline;
    }

    &::before {
        content: '';
        background-repeat: no-repeat;
        background-size: cover;
        width: 20px;
        height: 20px;
    }
}

.notifications {
    &.dot-show {
        &::after {
            position: absolute;
            content: '';
            background: #FF8A00;
            width: 8px;
            height: 8px;
            border-radius: 50%;
            top: 2px;
            right: 3px;
        }
    }

    &::before {
        background-image: url('../img/bell.svg');
    }
}

.coins {
    font-weight: 700;
    font-size: 13.7184px;
    line-height: 16px;
    text-decoration-line: underline;
    color: #FFFFFF;

    &::before {
        background-image: url('../img/coins.svg');
        margin-right: 6px;
    }
}

.hed-person {
    display: flex;
    align-items: center;
    background: #FFFFFF;
    border-radius: 24px;
    padding: 4px 12px;
    margin-right: 40px;

    &:hover {
        .hed-person__name {
            color: #FF8A00;
        }
    }

    &::after {
        content: '';
        background-image: url('../../img/arrow-orange.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: 24px;
        height: 24px;
        margin-left: 20px;
    }
}

.hed-person__icon {
    width: 52px;
    height: 52px;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 50%;
    }
}

.hed-person__name {
    font-weight: 700;
    font-size: 16px;
    line-height: 21px;
    color: #000000;
    margin-left: 20px;
    transition: color 0.3s ease-in-out;
}



.burger {
    height: 16px;
    width: 30px;
    justify-content: center;
    align-items: center;
    z-index: 350;
    cursor: pointer;
    transition: all .3s;
    display: none;
    position: relative;
    margin-left: auto;

    span {
        position: absolute;
        background: #761CBD;
        height: 3px;
        width: 30px;
        display: block;
        transition: all .3s;
        border-radius: 5px;

        &:nth-child(1) {
            top: 16px;
        }

        &:nth-child(2) {
            top: 50%;
            transform: translateY(-50%);
        }

        &:nth-child(3) {
            bottom: 16px;
        }
    }

    &.active {
        span {
            &:nth-child(1) {
                top: 6px;
                transform: rotate(45deg);
            }

            &:nth-child(2) {
                opacity: 0;
            }

            &:nth-child(3) {
                bottom: 7px;
                transform: rotate(-45deg);
            }
        }
    }
}


.hed-two {
    .burger {
        margin-left: 0;
    }
}

@media (max-width: 1400px) {
    .logo {
        width: 100px;
        display: block;

        img {
            max-width: 100%;
            max-height: 100%;
            object-fit: cover;
        }
    }

    .header__btn {
        font-size: 16px;
    }

    .lang-act {
        font-size: 16px;
    }

    .lang-act::after {
        width: 24px;
        height: 24px;
    }

    .lang__link {
        font-size: 16px;
        line-height: 18px;
    }

    .header-btns {
        margin-right: 50px;
    }

    .header {
        padding: 10px 0;
    }

    .hed-person__icon {
        width: 48px;
        height: 48px;
    }

    .hed-person__name {
        margin-left: 15px;
        font-size: 16px;
        line-height: 19px;
    }

    .hed-circle {
        padding: 8px;
    }

    .coins {
        font-size: 12px;
    }

    .hed-person {
        margin-right: 50px;
    }

    .hed-circle {
        margin-right: 30px;
    }

    .hed-person::after {
        margin-left: 15px;
    }
}

@media (max-width: 1023px) {
    .mob-menu {
        position: fixed;
        width: 100%;
        height: 100%;
        z-index: 2000;
        left: 0;
        top: 0;
        background-color: #FFFFFF;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 70px 15px;
        transform: scale(0.8);
        opacity: 0;
        pointer-events: none;
        transition: all 0.3s ease-in-out;

        &.active {
            transform: scale(1);
            opacity: 1;
            pointer-events: all;
        }
    }

    .header-btns {
        margin-right: 0;
        margin-left: 0;
        flex-direction: column;
        align-items: flex-start;
    }

    .header__btn:last-child {
        background-color: transparent;
        padding: 0;
        color: #761CBD !important;
        margin-top: 10px;
    }

    .lang {
        margin-top: 20px;

    }

    .lang-act {
        &.active {
            &::after {
                transform: rotateX(180deg);
            }
        }
    }

    .lang__list {
        opacity: 1;
        pointer-events: all;
        display: none;
        transition: none;
    }

    .burger {
        display: block;
        z-index: 2222222;
    }

    .hed-person__name {
        display: none;
    }

    .hed-person::after {
        margin-left: 10px;
    }

    .hed-circle {
        font-size: 0;
    }

    .coins::before {
        margin-right: 0;
    }

    .hed-person__icon {
        width: 40px;
        height: 40px;
    }

    .hed-person {
        margin-right: 20px;
    }


    .hed-two {
        .header-person-mob {
            display: flex !important;
        }
    }


    .hed-circle {
        margin-right: 20px;
    }

}

@media (max-width: 580px) {
    .logo {
        width: 80px;
    }

    .burger span {
        width: 25px;
    }
}