.app {
    background: url('../img/app-bg.jpg') center (center / cover) no-repeat;
    min-height: 498px;
    overflow: hidden;
}

.container {}

.app__wrapper {
    display: flex;
    justify-content: space-between;
    max-width: 1155px;
    transform: translateX(20px);
    position: relative;
    z-index: 1;

    &::before {
        position: absolute;
        content: '';
        width: 704px;
        height: 704px;
        background: #FF8A00;
        z-index: -1;
        border-radius: 50%;
        right: -229px;
        bottom: -286px;
    }
}

.app__left {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.app__text {
    font-weight: 400;
    font-size: 32px;
    line-height: 34px;
    color: #FFFFFF;


    span {
        font-weight: 700;
        padding-top: 17px;
        text-transform: uppercase;
        display: block;
    }
}

.app__download {
    margin-top: 40px;
    display: flex;

    a {
        display: block;
        margin-right: 30px;
        transition: all 0.3s ease-in-out;

        @media(any-hover: hover) {
            &:hover {
                opacity: 0.6;
            }
        }

        &:last-child {
            margin-right: 0;
        }
    }
}

.app__img {
    transform: translateY(5px);

    img {
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
    }
}


@media (max-width: 1400px) {
    .app__text {
        font-size: 32px;
        line-height: 36px;
    }

    .app__download {
        margin-top: 20px;
    }

    .app__download a {
        width: 180px;

        img {
            max-width: 100%;
            max-height: 100%;
            object-fit: cover;
        }
    }

    .app__img {
        width: 400px;

        img {
            max-width: 100%;
            max-height: 100%;
            object-fit: cover;
        }
    }

    .app {
        min-height: 425px;
    }

    .app__wrapper::before {
        width: 500px;
        height: 500px;
        right: -63px;
        bottom: -193px;
    }
}


@media (max-width: 920px) {
    .app__img {
        width: 350px;
    }

    .app {
        min-height: 376px;
    }
}

@media (max-width: 870px) {
    .app__wrapper {
        flex-direction: column;
        align-items: center;
        text-align: center;
        transform: translateX(0);
    }

    .app__download {
        justify-content: center;
    }

    .app {
        padding: 30px 0 0 0;
    }

    .app__download a {
        width: 120px;
    }

    .app__text {
        font-size: 28px;
        line-height: 28px;
    }
}