.sort {
    padding: 30px 0;
    background: #F8F3FB;

    .container {
        display: flex;
        align-items: center;
        flex-wrap: wrap;
        row-gap: 20px;
    }
}

.sort__left {
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    text-align: center;
    color: #606060;
    display: flex;
    align-items: center;
    margin-right: 40px;

    &::after {
        content: '';
        background-image: url('../../img/sort.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: 28px;
        height: 28px;
        margin-left: 15px;
    }
}

.custom-select {
    position: relative;
    border: 1px solid #BCBCBC;
    border-radius: 20px;

    &.sort-select {
        min-width: 216px;
        margin-right: 27px;
        background: #FFFFFF;
        border: 1px solid #FF8A00;

        .custom-select-value {
            font-weight: 700;
            font-size: 18px;
            line-height: 22px;
        }

        .custom-select__top {
            padding: 9px 40px;
            justify-content: center;

            &::after {
                right: 3px;
                top: 6px;
            }
        }
    }

    .custom-select__top {
        position: relative;
        cursor: pointer;
        display: flex;
        align-items: center;
        padding: 5px 15px;
        padding-right: 8px;
        width: 100%;

        &.active {
            &::after {
                transform: rotateX(180deg);
            }
        }

        &::after {
            position: absolute;
            content: '';
            background-image: url('../img/arrow-orange.svg');
            background-repeat: no-repeat;
            background-size: cover;
            width: 28px;
            height: 28px;
            display: block;
            transition: all 0.3s ease-in-out;
            margin-left: 10px;
            right: 8px;
            top: 8px;
        }
    }

    .custom-select-value {
        font-weight: 400;
        font-size: 18px;
        line-height: 30px;
        color: #000000;
    }

    .custom-select__list {
        position: absolute;
        background: #FFFFFF;
        box-shadow: 4px 4px 15px rgba(67, 0, 119, 0.18);
        border-radius: 10px;
        padding: 15px;
        width: 100%;
        top: calc(100% + 5px);
        display: none;
        z-index: 2;
    }

    .custom-select-item {
        font-weight: 600;
        font-size: 15px;
        line-height: 150%;
        color: #606060;
        white-space: nowrap;
        margin-bottom: 10px;
        cursor: pointer;
        transition: all 0.1s ease-in-out;

        &:hover {
            color: #FF8A00;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.sort-radio {
    font-weight: 700;
    font-size: 18px;
    line-height: 22px;
    text-align: center;
    color: #606060;
    position: relative;
    background: #FFFFFF;
    border: 1px solid #FF8A00;
    border-radius: 24px;
    padding: 9px;
    min-width: 216px;
    margin-right: 27px;
    transition: all 0.3s ease-in-out;

    &.active {
        color: #FFFFFF;
        background: #FF8A00;
    }

    &:hover {
        color: #FFFFFF;
        background: #FF8A00;
    }

    &:last-child {
        margin-right: 0;
    }

    input {
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        opacity: 0;
        cursor: pointer;
    }
}

.sort-radio-wrap {
    display: flex;
    flex-wrap: wrap;
    row-gap: 20px;
}


@media (max-width: 1400px) {
    .sort-radio {
        font-size: 14px;
        line-height: 18px;
        min-width: 170px;
        padding: 6px;
    }

    .custom-select.sort-select .custom-select-value {
        font-size: 14px;
        line-height: 18px;
    }

    .custom-select.sort-select .custom-select__top {
        padding: 7px 40px;
    }

    .custom-select.sort-select .custom-select__top::after {
        width: 24px;
        height: 24px;
        top: 4px;
    }

    .custom-select.sort-select .custom-select-item {
        font-size: 13px;
    }

    .sort__left {
        font-size: 16px;
    }

    .sort__left::after {
        width: 24px;
        height: 24px;
    }
}

@media (max-width: 580px) {
    .sort-radio {
        width: 100%;
        margin: 0;
    }

    .custom-select.sort-select {
        width: 100%;
        margin-right: 0;
    }
}