.row-item {
    background: #FFFFFF;
    box-shadow: 4px 4px 15px rgba(67, 0, 119, 0.18);
    border-radius: 30px;
    padding: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 30px;

    &:last-child {
        margin-bottom: 0;
    }
}

.person-row {
    align-items: center;

    .person-block__img {
        width: 80px;
        height: 80px;
    }

    .person-block .person-block__name {
        margin-top: 0;
        font-size: 26px;
        line-height: 32px;
        color: #761CBD;
    }

    .person-block .person-block__online {
        font-size: 14px;
        line-height: 16px;
    }

    .person-block {
        align-items: center;
    }
}

.star {
    display: flex;
    height: fit-content;

    &.star-1 {
        .star-item {
            &:nth-child(-n+1) {
                background-image: url('../img/star-fill.svg');
            }
        }
    }

    &.star-2 {
        .star-item {
            &:nth-child(-n+2) {
                background-image: url('../img/star-fill.svg');
            }
        }
    }

    &.star-3 {
        .star-item {
            &:nth-child(-n+3) {
                background-image: url('../img/star-fill.svg');
            }
        }
    }

    &.star-4 {
        .star-item {
            &:nth-child(-n+4) {
                background-image: url('../img/star-fill.svg');
            }
        }
    }

    &.star-5 {
        .star-item {
            &:nth-child(-n+5) {
                background-image: url('../img/star-fill.svg');
            }
        }
    }

    .star-item {
        width: 24px;
        height: 24px;
        margin-right: 10px;
        background-image: url('../img/star.svg');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
    }
}



.person-info {
    height: fit-content;
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 26px;
    line-height: 30px;
    color: #BCBCBC;

    div {
        display: flex;
        align-items: center;

        &::before {
            content: '';
            background-repeat: no-repeat;
            background-size: cover;
            width: 24px;
            height: 24px;
            display: block;
            margin-right: 10px;
            transform: translateY(4px);
        }
    }
}

.person-info__reviews {
    margin-right: 50px;

    &::before {
        background-image: url('../../img/message-circle.svg');
    }
}

.person-info__like {
    &::before {
        background-image: url('../../img/thumbs-up.svg');
    }
}

.person-row__btn {
    height: fit-content;
}


@media (max-width: 1400px) {
    .person-row .person-block .person-block__name {
        font-size: 24px;
        line-height: 28px;
    }

    .person-row .person-block .person-block__online {
        font-size: 12px;
        line-height: 14px;
    }

    .person-row .person-block__img {
        width: 70px;
        height: 70px;
    }

    .star .star-item {
        width: 22px;
        height: 22px;
    }

    .person-info {
        font-size: 22px;
        line-height: 24px;
    }

    .person-info div::before {
        width: 22px;
        height: 22px;
    }

    .person-row {
        padding: 20px;
    }
}

@media (max-width: 920px) {
    .person-row {
        display: grid;
        grid-template-columns: 1fr 200px;

        .star {
            justify-content: flex-end;
        }

        .person-row__btn {
            margin-left: auto;
        }
    }
}

@media (max-width: 580px) {
    .person-row {
        display: flex;
        flex-direction: column;
        align-items: center;

        .person-row__btn {
            margin-top: 20px;
            margin-left: 0;
        }
    }

    .person-info {
        margin-top: 20px;
    }

    .person-row .star {
        margin-top: 20px;
    }
}