.promo {
    position: relative;
    padding: 50px 0 100px 0;

    .container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}

.promo__bg {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    object-fit: cover;
    z-index: -1;
}



.promo__title {
    font-weight: 300;
    font-size: 40px;
    line-height: 50px;
    text-align: center;
    text-transform: uppercase;
    color: #FFFFFF;

    span {
        color: #FF8A00;
        font-weight: 700;
    }
}

.search {
    margin-top: 200px;
    max-width: 796px;
    width: 100%;
    height: 50px;
    position: relative;
    overflow: hidden;
}

.search__input {
    background: #F5F5F5;
    border-radius: 20px;
    font-weight: 600;
    font-size: 20px;
    line-height: 30px;
    color: #000000;
    padding: 0 20px 0 80px;
    width: 100%;
    height: 100%;
    border: none;
}

.search__btn {
    background-image: url('../img/search.svg');
    background-repeat: no-repeat;
    background-size: auto;
    background-position: center;
    position: absolute;
    left: 0;
    top: 0;
    width: 80px;
    height: 100%;
}

.promo-btns {
    margin-top: 20px;
    display: flex;
}

.promo__btn {
    margin-right: 50px;

    @media(any-hover: hover) {
        &:hover {
            color: #FFFFFF !important;
        }
    }

    &:last-child {
        margin-right: 0;
    }
}

.btn {}

.btn--purple {}


@media (max-width: 1400px) {
    .promo__title {
        font-size: 34px;
        line-height: 52px;
    }

    .search {
        height: 50px;
        margin-top: 100px;
    }

    .search__btn {
        background-size: 50%;
        width: 60px;
    }

    .promo {
        padding: 50px 0;
    }
}

@media (max-width: 580px) {
    .promo-btns {
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .promo__btn {
        margin-right: 0;
        width: 100%;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }
    }


    .promo {
        height: calc(100vh - 54px);
        padding-bottom: 150px;
    }

    .promo .container {
        height: 100%;
    }

    .search {
        margin-top: auto;
    }
}