.order-info {
    margin: 50px 0 100px 0;
}

.container {}

.order-info__wrapper {
    display: flex;
    justify-content: space-between;
}

.order-info__left {
    width: calc(100% - 360px);

    .order-box {
        margin-top: 30px;
    }
}

.order-info__top {
    display: flex;
    justify-content: space-between;
}

.order-info__title {
    font-weight: 700;
    font-size: 28px;
    line-height: 32px;
    color: #761CBD;
    margin-right: 30px;

    span {
        font-weight: 300;
        color: #BCBCBC;
    }
}

.order-info__code {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #BCBCBC;
}

.order-box {
    display: grid;
    grid-template-columns: repeat(3, 210px) 1fr;
    gap: 30px;
}

.order-box-item {
    background: #F8F3FB;
    border-radius: 20px;
    display: flex;
    align-items: center;
    padding: 12px;

    div {
        &:nth-child(2) {
            font-weight: 400;
            font-size: 14px;
            line-height: 19px;
            color: #606060;

            span {
                display: block;
                font-weight: 700;
            }
        }
    }
}

.order-box-item__icon {
    width: 52px;
    height: 52px;
    background: #FFFFFF;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
    padding: 14px;

    img {
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
    }
}

.price-block {
    background: linear-gradient(145.04deg, #761CBD 12.27%, #CD8EFF 87.55%);
    border-radius: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.price-item {
    font-weight: 900;
    font-size: 26px;
    line-height: 35px;
    color: #FFFFFF;

    span {
        color: #FF8A00;
    }
}

.order-info-task {
    margin-top: 50px;
}

.order-info-task-item {
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    color: #606060;
    margin-bottom: 50px;

    &:last-child {
        margin-bottom: 0;
    }

    p {
        font-weight: 700;
        padding-left: 10px;
        margin-left: 30px;
        border-left: 1px solid #761CBD;
        margin-top: 5px;
    }
}

.specialist {
    background: #FFFFFF;
    box-shadow: 4px 4px 15px rgba(67, 0, 119, 0.18);
    border-radius: 30px;
    width: 330px;
    padding: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 454px;
    height: fit-content;
    position: relative;

    &::before {
        content: '';
        position: absolute;
        background-image: url('../../img/select.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: 32px;
        height: 55px;
        right: 50px;
        top: 0;
    }

    &.specialist-not-select {
        padding-top: 65px;

        &::before {
            display: none;
        }

        .specialist__name {
            color: #E0E0E0;
            padding-bottom: 30px;
            border-bottom: 1px solid #E0E0E0;
        }
    }
}



.specialist__img {
    width: 90px;
    height: 90px;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 50%;
    }
}

.specialist__name {
    font-weight: 700;
    font-size: 18px;
    line-height: 21px;
    color: #761CBD;
    margin-top: 10px;
}

.specialist__responded {
    font-weight: 400;
    font-size: 13.7184px;
    line-height: 16px;
    color: #606060;
    margin-top: 30px;

    span {
        font-weight: 700;
    }
}

.specialist__title {
    font-weight: 700;
    font-size: 13px;
    line-height: 16px;
    color: #606060;
    margin-bottom: 20px;
}

.specialist-info {
    margin-top: 20px;
    display: flex;
    justify-content: space-between;
}

.specialist-info-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 40px;

    &:last-child {
        margin-right: 0;
    }

    &.specialist-info-item--reviews {
        .specialist-info-item__text {
            &::before {
                background-image: url('../img/message-circle2.svg');
            }
        }
    }

    &.specialist-info-item--like {
        .specialist-info-item__text {
            &::before {
                background-image: url('../img/thumbs-up2.svg');
            }
        }
    }
}



.specialist-info-item__num {
    width: 60px;
    height: 60px;
    background: #F8F3FB;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 700;
    font-size: 20px;
    line-height: 23px;
    text-align: center;
    color: #761CBD;
}

.specialist-info-item__text {
    font-weight: 700;
    font-size: 13.7184px;
    line-height: 16px;
    color: #606060;
    display: flex;
    align-items: center;

    &::before {
        content: '';
        background-repeat: no-repeat;
        background-size: cover;
        width: 24px;
        height: 24px;
        display: block;
        margin-right: 5px;
    }
}



.specialist__bottom {
    margin-top: 30px;
    padding-top: 30px;
    border-top: 1px solid #E0E0E0;

    div {
        font-weight: 400;
        font-size: 14px;
        line-height: 16px;
        color: #606060;
        margin-bottom: 10px;

        &:last-child {
            margin-bottom: 0;
        }

        span {
            font-weight: 700;
        }
    }
}

.specialist-contact {
    background: #F8F3FB;
    margin-top: 50px;
    padding: 20px 40px;
    min-height: 120px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.specialist-contact__left {
    white-space: nowrap;
}

.specialist-contact__btn {
    white-space: nowrap;
}

.specialist-contact__title {
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: #606060;
}

.specialist-contact__subtitle {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #606060;
}

.specialist-contact__center {
    background: #FFFFFF;
    border-radius: 20px;
    padding: 10px 20px;
    height: 100%;
    max-width: 795px;
    width: 100%;
    margin: 0 30px;

    a {
        font-weight: 400;
        font-size: 16px;
        line-height: 30px;
        color: #606060;
        display: block;
    }
}

.order-info-btns {
    margin-top: 30px;
    display: flex;
    flex-wrap: wrap;
    row-gap: 20px;
}

.order-info__btn {
    margin-right: 40px;

    &:last-child {
        margin-right: 0;
    }

    &:first-child {
        margin-right: auto;
    }
}



@media (max-width: 1550px) {
    .specialist {
        width: 310px;
    }

    .order-info__left {
        width: calc(100% - 340px);
    }
}

@media (max-width: 1450px) {
    .order-info__left .order-box {
        grid-template-columns: repeat(3, 220px) 1fr;
    }

    .order-box-item div:nth-child(2) {
        font-size: 15px;
    }

    .order-box-item__icon {
        width: 54px;
        height: 54px;
        margin-right: 15px;
    }
}

@media (max-width: 1400px) {
    .order-info-task-item {
        font-size: 16px;
        line-height: 26px;
    }

    .specialist-contact__title {
        font-size: 18px;
        line-height: 25px;
    }

    .specialist-contact__subtitle {
        font-size: 14px;
        line-height: 17px;
    }

    .specialist__name {
        font-size: 16px;
    }

    .price-item {
        font-size: 26px;
        line-height: 31px;
    }

    .order-info__title {
        font-size: 26px;
        line-height: 32px;
    }

    .specialist-contact__center a {
        font-size: 14px;
        line-height: 30px;
    }

    .order-info__left .order-box {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 1023px) {
    .order-info__wrapper {
        flex-direction: column;
    }

    .order-info__left {
        width: 100%;
        margin-bottom: 30px;
    }

    .specialist {
        width: 100%;
    }

    .order-info-task-item {
        margin-bottom: 30px;
    }

    .order-info__btn:first-child {
        width: 100%;
    }

    .specialist-contact {
        flex-direction: column;
        text-align: center;
    }

    .specialist-contact__center {
        margin-top: 20px;
    }

    .specialist-contact__btn {
        margin-top: 20px;
    }

    .order-info__left .order-box {
        gap: 20px;
    }
}

@media (max-width: 580px) {
    .order-info__left .order-box {
        grid-template-columns: 1fr;

        .price-block {
            height: 84px;
        }
    }

    .order-info__title {
        font-size: 22px;
        line-height: 26px;
    }

    .order-box-item {
        padding: 15px;
    }

    .order-info {
        margin: 40px 0;
    }


}