.category {}

.container {}

.category__wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 25px;
}

.category-item {
    background: #F8F3FB;
    border-radius: 30px;
    padding: 20px 40px 20px 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    transition: all 0.2s ease-in-out;
    padding: 20px;

    @media(any-hover: hover) {
        &:hover {
            background: #efdbfa;
        }
    }

    &::before {
        content: '';
        position: absolute;
        background-image: url('../../img/category-item.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: 75px;
        height: 75px;
        left: 0;
        top: 0;
    }
}

.category-item__text {
    font-weight: 700;
    font-size: 17px;
    line-height: 22px;
    color: #606060;
    margin-right: 30px;
}

.category-item__num {
    width: 90px;
    min-width: 90px;
    height: 90px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 900;
    font-size: 28px;
    line-height: 35px;
    color: #761CBD;
    position: relative;
    z-index: 1;
    transform: translateY(-5px);

    &::before {
        position: absolute;
        content: '';
        background: #FFFFFF;
        width: 100%;
        height: 100%;
        border-radius: 50%;
        left: 50%;
        top: 55%;
        transform: translate(-50%, -50%);
        z-index: -1;
    }
}


@media (max-width: 1300px) {
    .category__wrapper {
        grid-template-columns: repeat(3, 1fr);
        gap: 20px;
    }

    .category-item__num {
        width: 80px;
        min-width: 80px;
        height: 80px;
        font-size: 28px;
    }

    .category-item__text {
        font-size: 16px;
        line-height: 20px;
    }

    .category-item {
        padding: 15px 20px 15px 20px;
    }
}

@media (max-width: 880px) {
    .category__wrapper {
        grid-template-columns: repeat(2, 1fr);
    }


}

@media (max-width: 580px) {
    .category__wrapper {
        grid-template-columns: 1fr;
        gap: 10px;
    }

}