.entry-wrap-container {
    width: 100%;
}

.entry-wrap {
    margin: 50px auto 150px auto;
    background: #FFFFFF;
    box-shadow: 4px 4px 15px rgba(67, 0, 119, 0.18);
    border-radius: 30px;
    max-width: 795px;
    padding: 100px 140px;
    position: relative;

    .tabs-wrapper {}

    .tab-link-wrapper {
        display: flex;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        border: 1px solid #761CBD;
        border-radius: 20px;
        overflow: hidden;
    }

    .tab {
        font-weight: 700;
        font-size: 18px;
        line-height: 21px;
        color: #606060;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 10px;

    }

    .tab-active {
        background: #761CBD;
        color: #FFFFFF;
    }

    .tab-content-wrapper {
        margin-top: 30px;
    }

    .input-block {
        margin-bottom: 30px;

        input {
            &::placeholder {
                color: #606060;
            }
        }
    }

    .checkbox-block {
        margin-top: 30px;
    }

    .registr-form__btn {
        margin: 30px auto 0 auto;
    }

    .custom-select .custom-select-item {
        font-size: 16px;
    }
}

.entry-soc {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
}

.entry-soc__img {
    img {
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
    }
}

.registr-wrap {
    margin-top: 30px;
}

.tabs-content {
    display: none;
}

.tabs-content-active {
    display: block;
}






.or {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: #BCBCBC;
    text-align: center;
    margin: 35px 0;
    position: relative;

    span {
        padding: 0 30px;
        background-color: #FFFFFF;
        z-index: 22;
        position: relative;
    }

    &::before {
        position: absolute;
        content: '';
        border-top: 1px solid #BCBCBC;
        width: 100%;
        left: 0;
        top: 15px;
    }
}

.entry-form {}

.input-block {}

.input {}

.registr-form__btn {}

.btn {}

.btn--purple {}

.forgot-password {
    margin: 30px auto 0 auto;
    text-align: center;
}

.link--grey {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    text-decoration-line: underline;
    color: #BCBCBC;
    display: block;
    width: fit-content;

    &:hover {
        text-shadow: .5px 0 0 currentColor;
        color: #BCBCBC;
    }
}

.registration-link {
    position: absolute;
    bottom: -60px;
    left: 50%;
    transform: translateX(-50%);
}

.link--purple {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    text-decoration-line: underline;
    color: #761CBD;

    &:hover {
        text-shadow: .5px 0 0 currentColor;
        color: #761CBD;
    }
}

.entry-wrap__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 38px;
    color: #606060;
    text-align: center;
}

.entry-wrap__subtitle {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    color: #606060;
    margin-top: 30px;
}

.confirmation-registration__wrap {
    margin: 100px auto 0 auto;
    max-width: 290px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;

    input {
        width: 50px;
        height: 50px;
        border: 1px solid #761CBD;
        border-radius: 20px;
        text-align: center;
        padding: 10px;
        font-weight: 400;
        font-size: 20px;
        line-height: 30px;
        color: #606060;
        -moz-appearance: textfield;
        padding-bottom: 15px;

        &::-webkit-outer-spin-button,
        &::-webkit-inner-spin-button {
            -webkit-appearance: none;
        }
    }
}

.confirmation-registration__bottom {
    margin-top: 350px;
    text-align: center;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: #606060;
}

.send-link {
    display: block;
}

.create-password {
    margin-top: 50px;
}

.create-password__btn {
    margin: 50px auto 0 auto;
}


.choose-activity {
    margin-top: 50px;

    .custom-select {
        margin-bottom: 30px;
    }

    .checkbox-block {
        margin-top: 20px;
    }

    .link--grey {
        margin: 20px auto 0 auto;
    }
}







@media (max-width: 1400px) {
    .entry-wrap .tab {
        font-size: 16px;
        line-height: 18px;
    }
}


@media (max-width: 860px) {
    .entry-wrap {
        padding: 50px;
    }

    .entry-wrap__title {
        font-size: 22px;
        line-height: 30px;
    }

    .entry-wrap__subtitle {
        font-size: 16px;
        line-height: 23px;
        margin-top: 20px;
    }

}

@media (max-width: 580px) {
    .entry-soc {
        grid-template-columns: 1fr;
        gap: 10px;
    }

    .entry-wrap {
        padding: 50px 20px;
        width: 100%;
    }

    .entry-soc__img {
        text-align: center;
    }

    .entry-wrap .input-block {
        margin-bottom: 20px;
    }

    .entry-wrap .checkbox-block {
        margin-top: 20px;
    }

    .entry-wrap .registr-form__btn {
        margin-top: 20px;
    }

    .link--grey {
        font-size: 18px;
    }

    .link--purple {
        font-size: 18px;
    }

    .confirmation-registration__bottom {
        font-size: 16px;
        line-height: 30px;
        margin-top: 100px;
    }

    .confirmation-registration__wrap {
        margin-top: 50px;
        max-width: 250px;
    }

    .confirmation-registration__wrap input {
        width: 40px;
        height: 40px;
        border-radius: 10px;
    }

    .create-password {
        margin-top: 30px;
    }

    .create-password__btn {
        margin-top: 30px;
    }

    .choose-activity {
        margin-top: 30px;
    }
}