.how-work {
    background: #F8F3FB;
    padding-top: 30px;
    margin-top: 50px;
    padding-bottom: 0;
}

.sec-marg {}

.container {}

.how-work__title {}

.title {}

.how-work__wrapper {
    display: flex;
    justify-content: space-between;
    max-width: 1100px;
    margin: 30px auto 0 auto;
    position: relative;
    z-index: 2;

    &::before {
        position: absolute;
        content: '';
        border-top: 1px dashed #FF8A00;
        top: 37%;
        left: 0;
        width: 100%;
        z-index: -1;
    }
}

.how-work-item {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #F8F3FB;
}

.how-work-item__icon {
    width: 130px;
    height: 130px;
    background: linear-gradient(145.04deg, #761CBD 12.27%, #CD8EFF 87.55%);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    padding: 30px;

    img {
        max-width: 100%;
        max-height: 100%;
        object-fit: cover;
    }
}

.how-work-item__title {
    font-weight: 700;
    font-size: 22px;
    line-height: 26px;
    text-align: center;
    color: #606060;
    margin-top: 30px;
}

.how-work__bottom {
    background: #761CBD;
    padding: 35px 0;
    margin-top: 60px;

    .container {
        display: flex;
        justify-content: center;
        align-items: center;
        font-weight: 700;
        font-size: 30px;
        line-height: 25px;
        color: #FFFFFF;
    }
}

.how-work__btn {
    margin-left: 50px;

    &:hover {
        color: #FFFFFF !important;
    }
}

@media (max-width: 1400px) {
    .how-work-item__icon {
        width: 130px;
        height: 130px;
        padding: 30px;

        img {
            max-width: 100%;
            max-height: 100%;
            object-fit: cover;
        }
    }

    .how-work-item__title {
        margin-top: 15px;
        font-size: 18px;
        line-height: 22px;
    }

    .how-work__bottom .container {
        font-weight: 700;
        font-size: 26px;
    }

    .how-work__bottom {
        padding: 25px 0;
        margin-top: 40px;
    }
}


@media (max-width: 780px) {
    .how-work__wrapper {
        flex-direction: column;
    }

    .how-work__wrapper::before {
        display: none;
    }

    .how-work-item {
        margin-bottom: 40px;

        &:last-child {
            margin-bottom: 0;
        }
    }

    .how-work-item__icon {
        width: 110px;
        height: 110px;
    }

    .how-work__bottom .container {
        flex-direction: column;
    }

    .how-work__btn {
        margin-left: 0;
        margin-top: 10px;
    }

    .how-work__bottom .container {
        font-size: 22px;
        line-height: 32px;

    }

    .how-work__bottom {
        padding: 15px 0;
    }
}