.notification {
    margin: 50px 0 100px 0;
}

.notification__wrapper {
    margin-top: 30px;
}

.notification-item {
    display: flex;
    justify-content: space-between;
    padding: 20px 0;
    border-top: 1px solid #5c5c5c30;

    &:last-child {
        border-bottom: 1px solid #5c5c5c30;
    }

    .person-block {
        margin-right: 40px;
    }

    .person-block__img {
        width: 60px;
        min-width: 60px;
        height: 60px;
    }

    .person-block__name {
        font-size: 18px;
        line-height: 21px;
    }

    .person-block__online {
        font-size: 18px;
        line-height: 21px;
    }
}



.notification-item__date {
    font-weight: 400;
    font-size: 18px;
    line-height: 21px;
    color: #606060;
}

.notification__btn {
    margin: 30px auto 0 auto;
}

@media (max-width: 580px) {
    .notification-item .person-block__online {
        font-size: 16px;
        line-height: 19px;
    }

    .notification-item__date {
        font-size: 16px;
        line-height: 19px;
    }
}