.modal-wrap {
    position: fixed;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    background: rgba(0, 0, 0, 0.69);
    box-shadow: 10px 20px 50px rgba(0, 0, 0, 0.24);
    z-index: 20000000;
    opacity: 0;
    visibility: hidden;
    overflow-y: auto;
    transition: opacity 0.3s ease-in-out;
    display: flex;
    justify-content: center;
    align-items: center;

    &.active {
        opacity: 1;
        visibility: visible;
    }
}


.modal {
    @include scroll-hide;
    overflow-y: auto;
    height: fit-content;
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    max-height: 95vh;
    min-width: 440px;
    max-width: 440px;

    &.modal-edit {
        min-width: 1320px;
        max-width: 1320px;
    }

    &.modal-password-edit {
        min-width: 795px;
        max-width: 795px;
    }

    &.modal-balance-size {
        min-width: 795px;
        max-width: 795px;

        .modal__body {
            display: flex;
            flex-direction: column;
            align-items: center;
            min-height: 400px;
        }
    }
}


.modal__body {
    background: #FFFFFF;
    box-shadow: 4px 4px 15px rgba(67, 0, 119, 0.18);
    border-radius: 30px;
    padding: 50px;
    width: 100%;
    position: relative;
}

.modal__close {
    position: absolute;
    right: 20px;
    top: 20px;
    width: 36px;
    height: 36px;
    border-radius: 8px;
    cursor: pointer;
    transition: filter 0.2s ease-in-out;

    @media(any-hover: hover) {
        &:hover {
            filter: brightness(0);
        }
    }

    &::before {
        content: '';
        position: absolute;
        background-image: url('../img/close.svg');
        background-repeat: no-repeat;
        background-size: cover;
        width: 34px;
        height: 34px;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
}



.modal-edit__wrapper {
    display: flex;
}

.modal-edit__img {
    width: 570px;
    min-width: 570px;
    height: fit-content;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.modal-edit-form {
    width: calc(100% - 660px);
    max-width: 725px;
    margin-left: 50px;

    .custom-select {
        margin-top: 30px;
    }

    .custom-select .custom-select-item {
        font-size: 16px;
    }

    .textarea-block {
        max-width: 100%;

        textarea {
            height: 185px;

            &::placeholder {
                color: #000000;
            }
        }
    }

    .input-block {
        max-width: 100%;
    }
}

.modal-edit-form__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: #000000;

    .input-file {
        text-decoration: none;
    }
}


.modal-edit-form__bottom {
    margin-top: 30px;
    display: flex;
    justify-content: flex-end;
}

.modal-edit-form__btn {
    margin-right: 40px;

    &:last-child {
        margin-right: 0;
    }
}

.modal-password-edit {
    .modal__body {
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
    }
}

.modal-password-edit__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    color: #761CBD;
}

.modal-password-edit__descr {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: #606060;
    margin-top: 30px;
}

.modal-password-edit__btn {
    margin-top: 50px;
}


.modal-balance__title {
    font-weight: 700;
    font-size: 20px;
    line-height: 30px;
    color: #761CBD;
    margin-bottom: 30px;
}

.modal-balance__subtitle {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    color: #606060;
    max-width: 414px;
    padding-bottom: 25px;
}

.now-available {
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    text-align: center;
    color: #606060;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 25px;
    border-top: 1px solid #5c5c5c3c;
}

.now-available__wrap {
    display: flex;
    align-items: center;
    font-weight: 700;
    font-size: 19px;
    line-height: 23px;
    color: #FF8A00;
    margin-top: 5px;

    img {
        margin-right: 10px;
    }
}

.modal-balance__btn {
    margin-top: 30px;
}



.coins-radio {
    .radio-block {
        font-weight: 700;
        font-size: 20px;
        line-height: 23px;
        color: #606060;
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0;
        }

        input {
            width: 30px;
            height: 30px;
            border: 1px solid #761CBD;

            &::before {
                width: 20px;
                height: 20px;
                background: linear-gradient(145.04deg, #761CBD 12.27%, #CD8EFF 87.55%);
            }

            &:checked+img {
                filter: brightness(1);
                opacity: 1;
            }
        }

        img {
            margin-right: 10px;
            filter: brightness(0);
            opacity: 0.6;
            transition: all 0.3s ease-in-out;
        }
    }
}



.modal-balance {

    .price-block {
        font-weight: 400;
        font-size: 30px;
        line-height: 30px;
        color: #FFFFFF;
        padding: 35px 80px;
        margin-top: 30px;

        .price-item {
            margin-left: 10px;
        }
    }
}

.modal-balance--three {
    .price-block {
        margin-top: 0;
    }
}

.modal-balance-form {
    margin-top: 30px;
}

.modal-balance-form__wrap {
    max-width: 520px;
    display: grid;
    grid-template-columns: 275px 225px;
    gap: 20px;

    .input-block {
        display: flex;
        align-items: center;

        &::before {
            content: '';
            background-repeat: no-repeat;
            background-size: cover;
            width: 32px;
            min-width: 32px;
            height: 32px;
            display: block;
            margin-right: 18px;
        }
    }
}



.input-block--card {
    grid-column: 1 / 3;

    &::before {
        background-image: url('../img/card.svg');
    }
}

.input {}

.input-block--month {
    &::before {
        background-image: url('../img/month.svg');
    }
}

.input-block--cvv {
    &::before {
        display: none !important;
    }
}

.modal-balance-form__btn {
    margin: 30px auto 0 auto;
}

.modal-balance--four {
    .modal__body {
        justify-content: center;
    }
}





















@media (max-width: 1670px) {
    .modal.modal-edit {
        min-width: 95%;
    }

    .modal-edit__wrapper {
        margin-top: 20px;
    }
}

@media (max-width: 1400px) {
    .modal-edit__img {
        width: 480px;
        min-width: 480px;
    }

    .modal-edit-form {
        width: calc(100% - 500px);
        margin-left: 20px;
    }

    .modal-balance .price-block {
        font-size: 22px;
        padding: 20px;
        width: 100%;
    }

    .modal-balance__subtitle {
        font-size: 18px;
        line-height: 26px;
    }
}

@media (max-width: 1023px) {
    .modal-edit__wrapper {
        flex-direction: column;
    }

    .modal-edit__img {
        width: 100%;
        min-width: inherit;
        margin-bottom: 30px;
    }

    .modal-edit-form {
        width: 100%;
        margin-left: 0;
        max-width: 100%;
    }

    .modal.modal-password-edit {
        min-width: 95%;
    }

    .modal-password-edit__descr {
        margin-top: 20px;
        font-size: 16px;
    }

    .modal-password-edit__btn {
        margin-top: 20px;
    }

    .modal.modal-balance-size {
        min-width: 95%;
    }


}

@media (max-width: 680px) {
    .modal-balance-form__wrap {
        grid-template-columns: 1fr;
        max-width: 100%;
        width: 100%;
    }

    .modal-balance-form {
        width: 100%;
    }

    .input-block--card {
        grid-column: 1 / 2;
    }

    .input-block--cvv {
        padding-left: 48px;
    }
}


@media (max-width: 580px) {
    .modal__body {
        padding: 50px 20px 50px 20px;
    }

    .modal-edit-form__top {
        flex-direction: column;
        align-items: flex-start;
        font-size: 16px;
        line-height: 26px;
    }

    .modal-edit-form__top .input-file {
        margin-top: 10px;
    }

    .modal-edit-form__bottom {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
    }

    .modal-edit-form__btn {
        margin: 0;
        width: 100%;
    }

    .modal-edit-form .textarea-block textarea {
        height: 120px;
    }

    .modal-edit-form .custom-select {
        margin-top: 20px;
    }

    .modal-edit__img {
        margin-bottom: 20px;
    }

    .custom-select .custom-select-value {
        font-size: 16px;
        line-height: 26px;
    }

    .textarea-block textarea {
        font-size: 16px;
        line-height: 26px;
    }

    .input-block input {
        font-size: 16px;
        line-height: 26px;
    }
}