.offers__wrapper {
    margin-top: 50px;
}


.offers-row {
    .person-block {
        margin-top: 15px;

        .person-block__img {
            width: 60px;
            min-width: 60px;
            height: 60px;
        }
    }
}

.offers-row-left {
    min-width: 320px;
}

.offers-row-left__top {
    font-weight: 700;
    font-size: 22px;
    line-height: 26px;
    color: #761CBD;
    position: relative;
    display: flex;
    align-items: center;

    &::before {
        content: '';
        width: 16px;
        min-width: 16px;
        height: 16px;
        background: #FF8A00;
        border-radius: 50%;
        display: block;
        margin-right: 22px;
    }
}


.offers-row-center {
    display: flex;
    align-items: center;
    justify-content: space-between;
    max-width: 620px;
    width: 100%;
    margin: 0 30px;

    div {
        font-weight: 400;
        font-size: 20px;
        line-height: 26px;
        color: #606060;

        span {
            font-weight: 700;
        }
    }
}

.offers-row-right {
    width: 237px;
    min-width: 237px;
    display: flex;
    flex-direction: column;
    align-items: center;
    height: fit-content;
}

.offers-row__price {
    font-weight: 900;
    font-size: 26px;
    line-height: 32px;
    color: #FF8A00;
    display: flex;

    span {
        color: #761CBD;
        transform: translateY(-3px);
        display: block;
        padding-left: 5px;
    }
}

.offers-row__btn {
    margin-top: 15px;
}


@media (max-width: 1400px) {
    .offers-row__price {
        font-size: 22px;
    }


    div {
        font-size: 18px;
        line-height: 25px;
    }

    .offers-row-left__top {
        font-size: 22px;
        line-height: 28px;
    }
}

@media (max-width: 1200px) {
    .offers-row-center {
        flex-direction: column;
        height: fit-content;
    }

    .offers-row {
        align-items: center;
    }

    .offers-row-center {
        max-width: fit-content;
    }

    .offers-row-center div:nth-child(1) {
        margin-bottom: 10px;
    }

    .row-item {
        padding: 20px;
    }

    .row-item {
        border-radius: 20px;
    }
}

@media (max-width: 960px) {
    .offers-row-left {
        min-width: 260px;
    }

    .offers-row-right {
        width: 170px;
        min-width: 170px;
    }
}

@media (max-width: 860px) {
    .offers-row {
        flex-direction: column;
        align-items: center;
    }

    .offers-row-left {
        min-width: auto;
    }

    .offers-row-center {
        margin: 0;
        margin-top: 20px;
    }

    .offers-row-right {
        margin-top: 20px;
    }

    .offers-row-left__top::before {
        width: 10px;
        min-width: 10px;
        height: 10px;
        margin-right: 12px;
    }

    .offers__wrapper {
        margin-top: 30px;
    }

}