* {
    font-family: "Raleway",
        sans-serif;
}

body {
    &.scroll-hide {
        overflow: hidden;
    }
}

.container {
    max-width: 1400px;
    margin: 0 auto;
    padding: 0 15px;
}

.page {
    position: relative;
    overflow: hidden;
    min-height: 100vh;
    display: flex;
    flex-direction: column;

    &.hed-two {
        .header-person {
            display: flex;
        }

        .header-btns {
            display: none;
        }
    }
}

.btn {
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: #FFFFFF !important;
    display: flex;
    justify-content: center;
    align-items: center;
    width: fit-content;
    border-radius: 20px;
    padding: 10px 35px;
    border: 1px solid #00000000;
    transition: background 0.3s ease-in-out,
        color 0.3s ease-in-out;

    &:hover {
        color: #606060 !important;
        opacity: 1 !important;
    }

    &.btn--purple {
        background: #761CBD;

        &:hover {
            border: 1px solid #761CBD;
            background-color: transparent;
        }
    }

    &.btn--orange {
        background: #FF8A00;

        &:hover {
            border: 1px solid #FF8A00;
            background-color: transparent;
        }
    }

    &.btn--grey {
        border: 1px solid #BCBCBC;
        color: #BCBCBC !important;
    }

    &.btn--orange-border {
        border: 1px solid #FF8A00;
        background-color: transparent;
        color: #606060 !important;

        &:hover {
            background: #FF8A00;
            color: #FFFFFF !important;
        }
    }

    &.btn--purple-border {
        border: 1px solid #761CBD;
        background-color: transparent;
        color: #606060 !important;

        &:hover {
            background: #761CBD;
            color: #FFFFFF !important;
        }
    }
}

@media (max-width: 1400px) {
    .btn {
        padding: 8px 30px;
        font-size: 16px;
    }
}

.sec-marg {
    padding: 50px 0;

    @media (max-width: 1400px) {
        margin: 25px 0;
    }
}



.title {
    font-weight: 700;
    font-size: 28px;
    line-height: 32px;
    color: #606060;
    text-align: center;

    @media (max-width: 900px) {
        font-size: 26px;
        line-height: 34px;
    }

    @media (max-width: 580px) {
        font-size: 24px;
        line-height: 29px;
    }
}



.person-block {
    display: flex;

    &.online-show {
        .person-block__img {
            position: relative;

            &::before {
                position: absolute;
                content: '';
                width: 14px;
                height: 14px;
                border-radius: 50%;
                background: #1BBF00;
                border: 2px solid #fff;
                right: 5px;
                top: 10px;
            }
        }
    }

    .person-block__img {
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 50%;
        }
    }

    .person-block__info {
        margin-left: 20px;
    }

    .person-block__name {
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
        color: #606060;
        margin-top: 10px;
    }

    .person-block__online {
        font-weight: 400;
        font-size: 12px;
        line-height: 15px;
        color: #BCBCBC;
        margin-top: 5px;
    }
}


.pagination {
    margin: 50px auto 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;

    .pagination__arrow {
        width: 30px;
        height: 30px;
        background: #F8F3FB;
        border-radius: 50%;
        background-image: url('../img/arrow-pagin.svg');
        background-size: auto;
        background-repeat: no-repeat;
        background-position: center;
        transition: all 0.3s ease-in-out;

        &:hover {
            background-color: #e3c0f7;
        }
    }

    .arrow-prev {
        margin-right: 15px;
    }

    .arrow-next {
        margin-left: 15px;
        transform: rotate(180deg);
    }

    .pagination__link {
        font-weight: 400;
        font-size: 26px;
        line-height: 31px;
        display: flex;
        align-items: center;
        text-align: center;
        color: #761CBD;
        padding: 0 15px;

        @media(any-hover: hover) {
            &:hover {
                text-shadow: .5px 0 0 currentColor;
                color: #761CBD;
            }
        }
    }

    .pagination__link--active {
        font-weight: 700;
        pointer-events: none;
    }

    .pagination__link--dot {
        background-image: url('../img/dot.svg');
        width: 26px;
        height: 6px;
        background-repeat: no-repeat;

    }
}